@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}body{width:100%;max-height:100vh;font-size:1.6rem;font-family:"Ubuntu",sans-serif;background-color:rgba(26,25,26,0.9);color:#f8f1f1}.TodoCounter__title{text-align:center;font-size:5rem;margin-top:3rem}.TodoCounter__counter{margin-top:1rem;color:rgba(248,241,241,0.8);text-align:center;font-weight:400}.TodoSearch{display:block;width:45rem;max-width:45rem;height:5rem;margin:2rem auto;border:none;border-radius:0.5rem;outline:none;font-size:2rem;font-weight:bold;text-align:center;color:#f8f1f1;background-color:#1a191a}.TodoItem{display:flex;justify-content:space-between;align-items:center;width:45rem;max-width:45rem;min-width:32rem;height:5rem;margin:0 auto;margin-bottom:2rem;padding:0 2rem;border:none;border-radius:0.5rem;outline:none;box-shadow:0px 10px 5px -5px black;font-size:2rem;color:#f8f1f1;background-color:#1a191a}.TodoItem .Icon{border:none;outline:none;-webkit-transform:scale(1.2);transform:scale(1.2);cursor:pointer}.TodoItem .TodoItem-p{letter-spacing:1px}.TodoItem .TodoItem-p--completed{text-decoration:line-through}.TodoItem .Icon-delete{cursor:pointer;background-color:transparent;border:none;-webkit-transform:scale(1.6);transform:scale(1.6);transition:all 0.1s}.TodoItem .Icon-delete:hover{-webkit-transform:scale(1.8);transform:scale(1.8)}.TodoButton{display:block;position:relative;width:5rem;height:5rem;margin:0 auto;border:none;border-radius:50%;font-size:4rem;font-weight:500;color:#f8f1f1;background-color:#8236cb;cursor:pointer;z-index:9}.Modal{background-color:rgba(32,35,41,0.8);position:absolute;height:100vh;width:100%;top:0;display:flex;justify-content:center;align-items:center;color:#f4f4f4;z-index:1}.Form{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:0.8rem;overflow:hidden;width:40rem;height:auto;-webkit-animation:showForm 0.5s ease-in;animation:showForm 0.5s ease-in}.Form__label{width:100%;padding:1rem 0;text-align:center;font-size:2.5rem;font-weight:500;background-color:rgba(26,25,26,0.8)}.Form__textarea{width:100%;max-width:40rem;min-height:10rem;height:auto;padding-left:1rem;padding-top:0.5rem;font-size:2rem;font-weight:500;color:#f8f1f1;background-color:rgba(26,25,26,0.8);border:none;outline:none;resize:none}.Form__buttons{display:flex;justify-content:center;align-items:center;height:5rem;width:100%}.Form__buttons--cancel,.Form__buttons--submit{height:100%;flex:1 1;border:none;outline:none;cursor:pointer;font-size:2rem;font-weight:600}.Form__buttons--cancel{background-color:#c0392b;font-size:2rem}.Form__buttons--submit{background-color:#2ecc71}@-webkit-keyframes showForm{0%{opacity:0}50%{opacity:0.5}100%{opacity:1}}@keyframes showForm{0%{opacity:0}50%{opacity:0.5}100%{opacity:1}}.LoadingTodo-container{display:flex;justify-content:space-between;align-items:center;position:relative;width:45rem;max-width:45rem;min-width:32rem;height:5rem;margin:0 auto;margin-bottom:2rem;padding:0 2rem;box-shadow:0px 10px 5px -5px black;border-radius:0.5rem}.LoadingTodo-container .LoadingTodo-completeIcon{border:none;outline:none;-webkit-transform:scale(1.2);transform:scale(1.2);width:2.2rem;height:2.2rem;background-color:black;border-radius:0.2rem}.LoadingTodo-container .LoadingTodo-text{letter-spacing:1px;color:#1a191a}.LoadingTodo-container .LoadingTodo-deleteIcon{cursor:pointer;border:none;background-color:black;width:2.2rem;height:2.2rem;border-radius:0.2rem;-webkit-transform:scale(1.2);transform:scale(1.2);transition:all 0.1s}.LoadingTodo-container,.LoadingTodo-completeIcon,.LoadingTodo-deleteIcon{background:linear-gradient(90deg, rgba(250,250,250,0.1), #c8c7c7);background-size:400% 400%;-webkit-animation:loadingAnimation 3s ease-in-out infinite;animation:loadingAnimation 3s ease-in-out infinite}@-webkit-keyframes loadingAnimation{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes loadingAnimation{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}.EmptyTodo{text-align:center;font-size:2.5rem;font-weight:500;padding-bottom:1.5rem}.TodosError{text-align:center;font-size:2.5rem;font-weight:500;padding-bottom:1.5rem}

